
























import Vue from "vue";

export default Vue.extend({
  name: "Header",
  data() {
    return {
      menus: [
        ["Phonology", "audiotrack"],
        ["Converter", "sync_alt"],
        ["Phrasebook", "chat"],
      ],
    };
  },
  computed: {
    flag(): string {
      return this.$store.getters.languageRoot + "flag.png";
    },
  },
  methods: {
    navigate(path: string): void {
      if (this.$route.name !== path)
        this.$router.push({
          name: path,
          params: { lang: this.$route.params.lang },
        });
    },
  },
});
